<template>
  <a-card :bordered="false">
    <result :is-success="true" :description="description" :title="title">
      <template slot="action">
        <a-button type="primary" @click="continueHandout"></a-button>
        <!-- <a-button style="margin-left: 8px"></a-button>
        <a-button style="margin-left: 8px"></a-button> -->
      </template>
      <div>
        <div style="font-size: 16px; color: rgba(0, 0, 0, 0.85); font-weight: 500; margin-bottom: 20px;"></div>
        <a-row style="margin-bottom: 16px">
          <a-col :xs="24" :sm="12" :md="12" :lg="12" :xl="6">
            <span style="color: rgba(0, 0, 0, 0.85)"></span>
            20180724089
          </a-col>
          <a-col :xs="24" :sm="12" :md="12" :lg="12" :xl="6">
            <span style="color: rgba(0, 0, 0, 0.85)"></span>
            
          </a-col>
          <a-col :xs="24" :sm="24" :md="24" :lg="24" :xl="12">
            <span style="color: rgba(0, 0, 0, 0.85)"></span>
            2016-12-12 ~ 2017-12-12
          </a-col>
        </a-row>
        <a-alert type="info" :show-icon="true" style="margin-bottom:16px">
          <div slot="message">
            &nbsp;<a style="font-weight: 600">{{handoutResult?handoutResult.accountTotal:0}}</a>&nbsp;
            <a style="font-weight: 600">{{handoutResult?handoutResult.accountTrackTotal:0}}</a>&nbsp;
            <a style="font-weight: 600">{{handoutResult?handoutResult.caculatetotal:0}}</a>&nbsp;
            <!-- <a style="margin-left: 24px"></a> -->
          </div>
        </a-alert>
      </div>
    </result>
  </a-card>
</template>

<script>
  import Result from '@/components/Result/Result'
  import ACard from 'ant-design-vue/lib/card'
  import AButton from 'ant-design-vue/lib/button'
  import ARow from 'ant-design-vue/lib/row'
  import ACol from 'ant-design-vue/lib/col'
  import AAlert from 'ant-design-vue/lib/alert'
  import { mixinDevice,mixin } from '@/utils/mixin.js'
  import {mapActions} from 'vuex'

  const directionType = {
    horizontal: 'horizontal',
    vertical: 'vertical'
  }

  export default {
    name: 'Success',
    components: {
      Result,
      ACard,
      AButton,
      ARow,
      ACol,
      AAlert
    },
    mixins: [mixinDevice,mixin],
    data () {
      return {
        title: '',
        description: '\n' +
          '  Message \n' +
          ' \n' +
          ' ',
        directionType
      }
    },
    methods:{
        ...mapActions(['setCurrent']),
        continueHandout(){
            this.setCurrent(0)
        }
    }
  }
</script>

<style scoped>

</style>


